  }

  .cta-header {
    color: #FFF;
    font-family: Manrope;
    font-size: 7.396vw;
    font-style: normal;
    font-weight: 500;
    line-height: 100%; /* 7.396vw */
    letter-spacing: -0.37vw;
    text-transform: uppercase;

    span {
      font-family: Gallient;
      font-size: 6.615vw;
      font-style: normal;
      font-weight: 400;
      line-height: 100%;
      letter-spacing: -0.132vw;
      text-transform: uppercase;
    }
  }

  .cta-content {
    display: flex;
    flex-direction: column;
    gap: 1.823vw;
  }

  .cta-description {
    color: #404040;
    font-family: Manrope;
    font-size: 1.042vw;
    font-style: normal;
    font-weight: 500;
    line-height: 120%; /* 1.25vw */
