      text-transform: uppercase;
      width: 54.896vw;
    }
  }
  
  .get-closer_content{
    width: 100%;
    display: flex;
    gap: 3.333vw;
    justify-content: space-between;
    align-items: flex-end;
    
    p{
      width: 19.792vw;
    }
  }
  
  .get-closer_video-container{
    height: 29.167vw;
    width: 22.917vw;
    overflow: hidden;

    .get-closer_video-wrapper{
      height: 29.167vw;
