[fillable-text] {
  & > div {
    background: linear-gradient(
        to right,
        rgb(255, 255, 255) 50%,
        rgb(37, 37, 37) 50%
    );
    background-size: 200% 100%;
    background-position-x: 100%;
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
    /*   margin-bottom: -13.021vw; */
  }
}

